import axios from 'axios';
import React, {useState, useEffect} from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';

function Navbar() {

  const [breed, setBreed] = useState([])

  useEffect(() => {
    axios({
      method: "GET",
      url: 'http://127.0.0.1:8000/api/breed'
  }).then(response =>{
      setBreed(response.data)
      })
  },[])

  return (
     <nav className="navbar fixed-top navbar-expand-lg navbar-dark bg-dark fixed-top">
    <div className="container">
      <a className="navbar-brand" href="#">Бравые котята</a>
      <button className="navbar-toggler navbar-toggler-right" type="button" data-toggle="collapse" data-target="#navbarResponsive" aria-controls="navbarResponsive" aria-expanded="false" aria-label="Toggle navigation">
        <span className="navbar-toggler-icon"></span>
      </button>
      <div className="collapse navbar-collapse" id="navbarResponsive">
        <ul className="navbar-nav ml-auto">
          <li className="nav-item">
            <a className="nav-link" href="#">Главная</a>
          </li>
            <li className="nav-item dropdown">
            <a class="nav-link dropdown-toggle" href="#" id="navbarDropdownBlog" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
              Породы котеек
            </a>
            <div className="dropdown-menu dropdown-menu-right" aria-labelledby="navbarDropdownBlog">
              <a className="dropdown-item" href="#">Все породы</a>
            </div>
            </li>
          <li className="nav-item">
            <a className="nav-link" href="#">Волонтёрство</a>
            </li>
            <li className="nav-item">
            <a className="nav-link" href="#">О нас</a>
          </li>
          {breed.map(x=>(
                  <li className="nav-item">
                  <a className="nav-link" href="#" key={x.id}>{x.breed_name}
                  </a>
                  </li>
              ))}
          </ul>
      </div>
    </div>
  </nav>
  );
}

export default Navbar;